import { LucideGithub, LucideLinkedin, Mail } from "lucide-react"

export function Footer() {
  return (
    <footer className="py-12 px-4 border-t border-border bg-muted/10">
      <div className="max-w-7xl mx-auto">
        <div className="flex flex-col md:flex-row items-center justify-between gap-6">
          <div className="text-center md:text-left space-y-2">
            <h3 className="text-2xl font-bold">DeepChessIQ</h3>
            <p className="text-sm text-muted-foreground">Engineered for smarter chess learning</p>
            <p className="text-xs text-muted-foreground">Loyalist College 2025 Capstone Project</p>
          </div>

          <div className="flex items-center gap-4">
            <button
              type="button"
              className="w-10 h-10 rounded-lg bg-primary/10 hover:bg-primary/20 flex items-center justify-center transition-colors"
              aria-label="GitHub"
            >
              <LucideGithub className="w-5 h-5 text-primary" />
            </button>
            <button
              type="button"
              className="w-10 h-10 rounded-lg bg-primary/10 hover:bg-primary/20 flex items-center justify-center transition-colors"
              aria-label="LinkedIn"
            >
              <LucideLinkedin className="w-5 h-5 text-primary" />
            </button>
            <button
              type="button"
              className="w-10 h-10 rounded-lg bg-primary/10 hover:bg-primary/20 flex items-center justify-center transition-colors"
              aria-label="Email"
            >
              <Mail className="w-5 h-5 text-primary" />
            </button>
          </div>
        </div>

        <div className="mt-8 pt-8 border-t border-border text-center text-sm text-muted-foreground">
          <p>© 2025 DeepChessIQ. Built with Next.js, React and RL Chess Engines.</p>
        </div>
      </div>
    </footer>
  )
}
